import React, { Component } from 'react'
import PropTypes from 'prop-types'
import NewsItem from './NewsItem'

export default class News extends Component {
    static propTypes = {
        prop: PropTypes
    }
    constructor(){
        super();
        this.state = {
            articles:[],
            loading:false,
            page:1,
            pageSize:20
        }
    }

    async componentDidMount(){
        this.populateNews();

    }

    populateNews = async()=>{
        let url = `https://newsapi.org/v2/everything?domains=wsj.com&apiKey=d42f19357fbd47a1903eb73c323b65cf&page=${this.state.page}&pageSize=${this.state.pageSize}`;
        let data = await fetch(url);
        let parseData = await data.json();
        this.setState({articles:parseData.articles});
    }
    NextPage = async()=>{
        let url = `https://newsapi.org/v2/everything?domains=wsj.com&apiKey=d42f19357fbd47a1903eb73c323b65cf&page=${this.state.page+1}&pageSize=${this.state.pageSize}`;
        let data = await fetch(url);
        let parseData = await data.json();
        this.setState({articles:parseData.articles});
        this.setState({
            page:this.state.page +1
        });
      


    }

    PrevPage = async()=>{
        let url = `https://newsapi.org/v2/everything?domains=wsj.com&apiKey=d42f19357fbd47a1903eb73c323b65cf&page=${this.state.page-1}&pageSize=${this.state.pageSize}`;
        let data = await fetch(url);
        let parseData = await data.json();
        this.setState({articles:parseData.articles});
        this.setState({
            page:this.state.page +1
        });


    }
  

    render() {
        return (
          <div className="container mt-5">
                <div className="row">
                    {this.state.articles.map((element)=>{
                        return     <div key={element.url} className="col-sm-4">
                        <NewsItem  title={element.title} description={element.description} url={element.url} urlToImage={element.urlToImage} />
                        </div>

                        
                    })}
                    <button type="button" onClick={this.NextPage} className="btn btn-success">next</button>
                    <button type="button" onClick={this.PrevPage} className="btn btn-success">prev</button>

            
            </div>
          </div>
        )
    }
}
